.bg{
    background-image: -webkit-gradient(linear, left top, right top, from(#ee6e73), to(#ee6eb4));
    background-image: -webkit-linear-gradient(left, #ee6e73, #ee6eb4);
    background-image: -o-linear-gradient(left, #ee6e73, #ee6eb4);
    background-image: linear-gradient(to right, #ee6e73, #ee6eb4);
    color: white;
}

.exit {
    font-size: 1.8rem;
    padding-top: .8rem;
}


